.about-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  margin: 0 auto;
  padding-top: 8.3rem;
  padding-bottom: 12rem;
  background-color: #e9c46a;
  background-image: linear-gradient(135deg, #e9c46a, #f4a261);
  color: white;
  clip-path: polygon(0 10%, 100% 0, 100% 90%, 0 100%);
}

h2 {
  font-size: 5rem;
  font-weight: 700;
  margin-bottom: 2.4rem;
  text-transform: uppercase;
}

.about-button {
  background-color: #6c91f7;
  border: none;
  cursor: pointer;
  padding: 1.6rem 1.9rem;
  color: #ffffff;
  font-size: 1.6rem;
  border-radius: 0.8rem;
  font-weight: 600;
  margin-top: 2.3rem;
}

.about-button:hover {
  background-color: #4172f6;
  transform: translatey(-3px);
  box-shadow: rgba(0, 0, 0, 0.25) 2.35px 2.5px 2.6px;
  transition: background-color 0.2s ease-in-out;
}

.about-div {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5rem;
}

.about-img {
  border-radius: 50%;
  width: 33rem;
  height: 33rem;
  object-fit: cover;
}

.about-text-section {
  width: 50rem;
  font-size: 1.7rem;
  font-weight: 600;
}

@media screen and (max-width: 760px) {
  .about-div {
    flex-direction: column;
  }

  .about-text-section {
    width: 68vw;
    font-size: 2.6vw;
    font-weight: 600;
  }

  .about-img {
    border-radius: 50%;
    width: 45vw;
    height: 45vw;
    object-fit: cover;
  }

  .about-button {
    justify-self: center;
    font-size: 2.6vw;
    padding: 2vw 3vw;
  }
}

.about.dark {
  background-color: rgb(24, 23, 36);
}

@media screen and (max-width: 760px) {
  .about-text-section {
    font-size: 3vw;
  }
}
